.profileDataField img {
    width: 30px;
    margin-right: 10px;
}
.informationShort {
    display: flex;
    align-items: flex-start;
}
.informationShort img {
    margin: 2.5px 10px 0 0;
}
.listContacts {
    width: 750px;
    /* display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 20px; */
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 20px;
}
.informationUserShort {
    font-size: 18px;
    width: 725px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 25px;
    margin-bottom: 20px;
    border-bottom: 1px solid #3535352c;
    margin-bottom: 10px;
    padding-bottom: 10px;
}
.descriptionInformation, .contactField div {
    width: 300px;
    padding: 5px;
    max-width: 400px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: inset 0px 1px 1px 2px ghostwhite;
}
.descriptionInformation {
    white-space: pre-line;
    word-break: break-word;
    word-wrap: break-word;
}
.contactField {
    display: flex;
    align-items: center;
    margin: 5px 0;
    margin-right: 25px;
}
.contactField div {
    width: 300px;
    font-size: 17px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.profileDataField button {
    border: none;
    width: max-content;
    height: 35px;
    border-radius: 7.5px;
    background-color: #353535;
    color: #fff;
    font-size: 16px;
    margin: 5px;
    margin-left: 0;
    padding: 5px 15px;
}
.profileDataField button:hover {
    background-color: #505050;
}